.DotNavContainer{
    position: absolute;

    text-align: center;

    top: 50%;
    left: 85%;

    transform: translate(-50%, -50%);

    z-index: 1;
}

.Dot{
    list-style: none;
    width: 15px;
    height: 15px;
    background-color: rgba(187, 42, 42, 0.363);
    border-radius: 50%;
    cursor: pointer;

    margin-top: 3px;
}

.Dot_Active{
    list-style: none;
    width: 15px;
    height: 15px;
    background-color: rgb(187, 42, 42);
    border-radius: 50%;
    cursor: pointer;

    margin-top: 3px;
}

.Dot:hover{
    background-color: rgba(0, 0, 0, .1);
}

@media screen and (max-width: 1600px)
{
    .DotNavContainer{
        display: none;
    }
}