.topnav_background{
    position: fixed;

    min-width: 483px;

    display: flex;

    width: 100vw;
    height: 70px;

    overflow: hidden;

    background-color: black;

    z-index: 1000;
  }

  .topnav{
    display: flex;

    width: 100%;
    height: 100%;

    align-items:center;
    justify-content: space-between;

    margin: 0 auto 0 auto;
  }
  
  .topnav a {
    display: flex;
    color:white;
    
    text-decoration: none;
    font-size: 17px;
  }
  
  .topnav a.leftAtt{
    margin-left: 10px;
  }

  .topnav a.Button{
    display: flex;

    cursor: pointer;

    transform: translate(-50%);
  }

  .topnav_icon{
    display: flex;
    
    align-items: center;

    list-style: none;

    margin-right: 30px;


  }

  .topnav_icon img{
    width: 40px;
    height: 40px;

    margin: 3px;
  }

  .topnav_icon_small{
    display: none;

    width: 40px;
    height: 36px;
  }
  
  .topnav a.active {
    background-color: #4CAF50;
    color: white;
  }

  .Title{
     width: auto;
     height: auto;

     margin-top: 5px;
  }

  .Youtube {
    background-image: url(https://www.youtube.com/channel/UCCM8oRwSn5KdywxiuLp-bfA);
  }
  .Instagram {
    background-image: url(https://www.instagram.com/kah.cg/);
  }
  .on {
    display: none;
  }

  .layer_popup {
    display: none;
  }

  @media screen and (max-width: 1200px)
  {
    .topnav{
      display: flex;
      max-width: 1200px;
  
      width: 100%;
      height: 100%;
  
      align-items:center;
  
      margin: 0 auto 0 auto;
    }
  }

  @media screen and (max-width: 500px) {

    .topnav{
      display: flex;
      max-width: 1200px;
  
      width: 100%;
      height: 100%;
  
      align-items:center;
  
      margin: 0 auto 0 auto;
    }

    .topnav a.Button{
      display: none;
    }

    .topnav_icon {
      display: none;
    }
    .topnav_icon_small{
      display: flex;
  
      margin-left: auto;
      align-items: center;
  
      list-style: none;
  
      margin-right: 20px;
    }

    .on {
      display: block;
    }

    .layer_popup{ 
      position: fixed; 
      top: 0; 
      left: 0; 
      right: 0; 
      bottom: 0; 
    } 

    .layer{
      color: white;
      width: 100%; 
      height: 100vh;
      position: absolute; 
      top: 0; 
      left: 0; 
      bottom: 0; 
      right: 0; 
      margin: auto; 
      border-radius: 5px; 

      z-index: 100;
    } 

    .menu_bar{ 
      width: 100%;
      height: 90%;
      display: flex;
      justify-content: center;
      padding-top: 15%;
    }

    .menu_item{
      background: none;
    }

    .menu_item ul{
      width: 100%;
      text-align: center;
      padding: 0;
    }

    .menu_item a{ 
      color: white;
      font-family: "JS Dong Kang KR";
      font-weight: normal;
      font-size: 20px; 
      line-height: 1.5; 
      text-align: justify;
    }  

    .menu_item a:hover{ 
      background: rgba(148, 25, 25, 0.932);
    }  

    .btn_area{ 
      padding: 30px; 
      display: table; 
      table-layout: fixed; 
      width: 100%; 
      box-sizing: border-box; 
    }
 
    .btn_close{
      color: white; 
      border: none; 
      font-size: 15px;
      width: 80px;
      height: 45px;
      background: transparent;
      position: absolute;
      top: 10px; 
      right: 10px; 
      cursor: pointer; 
      font-weight: bold; 
      }
      .dimmed{ 
        background-color: rgba(0, 0, 0, 0.7); 
        position: absolute; 
        left: 0; 
        right: 0; 
        top: 0; 
        bottom: 0; 
        z-index: -1; 
      }
      .title {
        text-align: center;
        font-size: 30px;
        font-weight: bold;
      }
      .maker {
        display: flex;
        justify-content: space-around;
        align-items: center;
        text-align: center;
        margin-top: 50px;
        font-size: 25px;
      }
  }