.SliderContainer{
    position: absolute;

    min-width: 483px;
    min-height: 400px;

    width: 1200px;
    height: 62%;
    
    transform: translate(-50%, -50%);

    top: 50%;
    left: 50%;
}

.SliderCard{
    position: absolute;

    width: 100%;
    height: 100%;

    background-image: url(image/GameExhibit.png);
    background-size:cover;
    background-repeat: no-repeat;

    pointer-events: auto;

    display: flex;

    align-items: center;
    justify-content: center;
}

.grid-container{
    width: 90%;
    height: 97%;

    display: grid;
    grid-template-areas: 
    'GamePoster GameName'
    'GamePoster main'
    'GamePoster main'
    'GamePoster main'
    'GamePoster Platform';

    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr;

}

.SliderCard a{
    z-index: inherit;
}
.GamePoster{
    grid-area: GamePoster;
    text-align: center;
}

.GameName{
    grid-area: GameName;
    text-align: center;
}

.Content{
    display: flex;

    grid-area: main;

    align-items: center;
}

.Platform{

}

.GamePoster img{
    position: absolute;

    width: 450px;
    height: 350px;

    top:50%;

    transform: translate(-50%, -50%);
}

.SliderCard a.gameName{
    position: absolute;
    pointer-events: none;

    font-family:"JS Dong Kang KR";
    font-weight: bold;
    font-size: 40px;
    color: white;
    top: 4%;

    transform: translate(-50%);
}

.SliderCard a.context{
    position: relative;

    pointer-events: none; 

    font-family:"JS Dong Kang KR";
    font-weight: normal;
    font-size: 20px;
    color: white;

    left: 1%;
}

.SliderCard a.icon{
    position: absolute;

    width: 100px;
    height: 100px;

    left: 80%;
    top: 70%;
}

.Platform a{
    position: absolute;

    width: min-content;
    height: min-content;

    right: 5%;
}

.Platform img{
    width: 50px;
    height: 60px;
}

@media screen and (max-width: 120px)
{
    .SliderContainer{
        position: absolute;

        width: 100vw;
        height: 660px;
        
        transform: translate(-50%, -50%);
    
        top: 50%;
        left: 50%;
    }
}