.Poll{
    height: min-content;
}

.PollContainer{
    position: relative;
    display: flex;

    min-width: 483px;
    min-height: 400px;

    width: 1300px;
    height: 900px;

    left: 50%;

    justify-content: center;
    align-items: center;

    background-color: khaki;
    
    transform: translate(-50%, 0%);
}

.PollContainer a{
    color: white;
}



@media screen and (max-width: 1200px)
{
    .PollContainer{
        position: relative;
        display: flex;
    
        min-width: 483px;
        min-height: 400px;
    
        width: 100vw;
        height: 900px;
    
        justify-content: center;
        align-items: center;
    
        background-color: khaki;
        
        transform: translate(-50%, 0%);
    }
}

