.PollContent{
    position: relative;
    display: flex;
    
    align-items: center;
    justify-content: center;
    text-align: center;

    padding: 10px;

    height:90%;

}

.PollContent div{
    display: grid;
}

.PollContent a{
    position: relative;

}
.PollContent img{
    position: relative;

    width: 300px;
    height: 300px;
}

.VoteResult:not(.PollContainer){
    position: absolute;

    width: 90%;
    height: 55%;

    top:25%;
}

.Info:not(.PollContainer){
    position: absolute;
    display: flex;

    align-items: center;
    justify-content: center;

    left: 4%;

    top:20%;

    z-index: 1;
}

.Info a{
    font-family: "JS Dong Kang KR";
    font-weight: bold;
    font-size: 30px;
    
}

.Info button{
    margin-left: 10px;
}

.PollContent button{
    position: relative;

    width: 50%;

    transform: translate(50%, 0%);
}