.background
{
    background-color: black;
}

.main{
    position: relative;

    display: flex;

    max-width: 1920px;
    min-width: 483px;

    min-height: 900px;

    width: 1920px;
    height: max-content;

    align-items: center;
    justify-content: center;

    background-color: black;

    transform: translate(0%, 69px);

    z-index: 2;
}

.main div.GameInfo{
    position: relative;

    max-width: 1920px;
    min-width: 483px;

    min-height: 900px;

    width: 1920px;
    height: 90vh;

    overflow: hidden;

    z-index: 1;
}

.main div.Vote{
    position: relative;

    max-width: 1920px;
    min-width: 483px;

    min-height: 900px;

    width: 1920px;
    height: inherit;

    display: none;
    overflow: hidden;

    justify-content: center;

    z-index: 1;
}

.LeftDeco{

    position: absolute;

    width: 300px;
    height: 650px;

    top: 57%;
    left: 5%;
    transform: translate(0%, -50%);
}

.RightDeco{

    position: absolute;

    width: 300px;
    height: 650px;

    top: 57%;
    right: 5%;

    transform: translate(0%, -50%);
}

.RightButton{
    position: absolute;

    width: 20px;
    height: 20px;

    top: 50%;

    transform: translate(50%, 50%);

}

.LeftButton{
    position: absolute;

    width: 20px;
    height: 20px;
}

@media screen and (max-width: 1200px)
{
    .main{
        position: relative;
    
        display: flex;
    
        max-width: 1920px;
        min-width: 483px;
    
        min-height: 900px;
    
        width: auto;
        height: max-content;
    
        align-items: center;
        justify-content: center;
    
        background-color: black;
    
        transform: translate(0%, 69px);
    
        z-index: 2;
    }

    .LeftDeco{

        position: absolute;
    
        width: 300px;
        height: 650px;
    
        top: 57%;
        left: 5%;
        transform: translate(0%, -50%);

        display: none;
    }
    
    .RightDeco{
    
        position: absolute;
    
        width: 300px;
        height: 650px;
    
        top: 57%;
        right: 5%;
    
        transform: translate(0%, -50%);

        display: none;
    }


    .main div.GameInfo{
        position: relative;
    
        max-width: 1920px;
        min-width: 483px;
    
        min-height: 900px;
        
        width: 100vw;
    
    }

    .main div.Vote{
        position: relative;
    
        max-width: 1920px;
        min-width: 483px;
    
        min-height: 900px;
    
        width: inherit;
        height: inherit;
    
        display: none;
    
        justify-content: center;
    
        z-index: 1;
    }
}
